<template>
  <div>
      <ProductItem
      v-for="product in products"
      :product="product"
      ></ProductItem>
  </div>
</template>
<script>
import ProductItem from './ProductItem.vue'
export default {
  name: 'ProductList',
  components: {
    ProductItem
  },
  data() {
    return {
      products: [
        {
          id: 1,
          title: 'Product #1',
          description: 'lalala',
          content: '#content'
        },
        {
          id: 2,
          title: 'Product #2',
          description: 'lalala',
          content: '#content'
        },
        {
          id: 3,
          title: 'Product #3',
          description: 'lalala',
          content: '#content'
        },
        {
          id: 4,
          title: 'Product #4',
          description: 'lalala',
          content: '#content'
        }
      ]
    }
  }
}
</script>
<style scoped>
</style>



