<template>
    <div class="card">
      <h1>aaaas</h1>
        <div
         class="card-body"
         @click="info(data.id)">
            <h4 class="card-title"> {{data.title}}</h4>
            <p class="card-text">{{data.description}} items for {{data.content}}</p>
        </div>
    </div>
</template>
<script>
export default {
name: 'ProductItem',
props: {
    product: {
        type: Object,
        required: true,
    }
},
data() {
    return{
      data: this.product
    }
},
methods: {
  info: function (id) {
    console.log(id)
    this.$router.push('/info')
  }
}
}
</script>
<style>
    div.card {
        width: 500px;
        border: 1px solid red;
        border-radius: 10px;
        margin: 10px;
        padding: 10px;
    }
</style>



